<template>
  <div class="vide-content">
    <video id="myVideo" class="video-js" >
        <source :src="deta" type="video/mp4" >
    </video>
  </div>
</template>

<script>
//   :deta="data"  
//import myHead from "./head.vue"
export default {
   components: {
  },
  data() {
    return {
      
    };
  },
  props: {
    //
    deta: {
      default: function() {
        return '';
      }
    }
  },
  beforeCreate(){
    //data
  },
  created() {
    //,,data
  },
  methods: {
    //
    //clickdrawFeatures
    initVideo() {
        //
        let myPlayer = this.$video(myVideo, {
            //autoplayPlayer API
            controls: true,
            //,muted:
            autoplay: "muted",
            //<video>
            preload: "auto",
        });
    }
  },
  
  mounted() {
    //
    this.initVideo();
  }
};
</script>

<style scoped>
.vide-content{
    width: 100%;
    height: 100%;
    
}
#myVideo{
    width: 100%;
    height: 100%;
}
</style>



