<template>
  <div>
    <div class="swiper-video animated fadeInDown">
      <swiper_video  :deta="videos"></swiper_video>
    </div>
    <div class="sub-content">
      <div class="sub-text">
        <div class="sub-title"></div>
        <ul class="a-content" :style="{minHeight: max_height+ 'px'}">
          <li ref="item" class="item" v-for="(key,i) in article" :key="i" :style="{left:item_coor[i].left + 'px',top:item_coor[i].top + 'px'}">
            <div>
              <router-link to="">
                <p class="a-img"><img @load="quireCoord(key.id)" :src="key.img_url" alt=""></p>
                <p class="a-title">{{key.title}}</p>
                <p class="a-time">{{key.time}}</p>
                <p class="a-text" v-html="key.text"></p>
                <p class="a-gauze"></p>
              </router-link> 
            </div>
          </li>
        </ul>
      </div>
      <div class="person">
        <div class="sub-title"></div>
        <right_nav></right_nav>
      </div>
    </div>
  </div>
</template>

<script>
//   :deta="data"  
import swiper_video from "@/components/basis/swiper_video"
import right_nav from "@/components/basis/right_nav"
//swiper
export default {
   components: {
     swiper_video,
     right_nav
  },
  data() {
    return {
      time : '',
      videos:[
        {id:1,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:2,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:3,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:4,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:5,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'}
      ],
      article:[
        {id:1,bg_class:'1',sm_class:'1',title:'1',time:'2019-10-25 15:14',img_url:'/static/img/article/1.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/1.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/11.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:2,bg_class:'1',sm_class:'2',title:'2',time:'2019-10-25 15:14',img_url:'/static/img/article/2.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/2.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/10.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:3,bg_class:'1',sm_class:'3',title:'3',time:'2019-10-25 15:14',img_url:'/static/img/article/3.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/3.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/9.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:4,bg_class:'1',sm_class:'1',title:'4',time:'2019-10-25 15:14',img_url:'/static/img/article/4.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/4.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/8.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:5,bg_class:'1',sm_class:'2',title:'5',time:'2019-10-25 15:14',img_url:'/static/img/article/5.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/5.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/7.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:6,bg_class:'2',sm_class:'1',title:'6',time:'2019-10-25 15:14',img_url:'/static/img/article/6.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/6.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/6.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:7,bg_class:'2',sm_class:'2',title:'7',time:'2019-10-25 15:14',img_url:'/static/img/article/7.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/7.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/5.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:8,bg_class:'2',sm_class:'3',title:'8',time:'2019-10-25 15:14',img_url:'/static/img/article/8.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/8.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/4.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:9,bg_class:'2',sm_class:'1',title:'9',time:'2019-10-25 15:14',img_url:'/static/img/article/9.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/9.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/3.jpg" alt=""><br><br><br><br><br><br><br>'},
        {id:10,bg_class:'2',sm_class:'2',title:'10',time:'2019-10-25 15:14',img_url:'/static/img/article/10.jpg',text:'2019<br>3.72003<br>5000320<br>2014IUCN<br><img src="/static/img/article/10.jpg" alt=""><br><br><br><br><br><br><br><br><img src="/static/img/article/2.jpg" alt=""><br><br><br><br><br><br><br>'}
      ],
      col:2,  //
      col_height:[], //
      col_width:'', //item
      item_height:[],//item 
      item_coor:[],  //item
      item_btm:10,  //
      max_height:'', //
      screenWidth: document.body.clientWidth, //
    };
  },
  props: {
    //
    deta: {
      default: function() {
        return '';
      }
    }
  },
  methods: {
    //
    //clickdrawFeatures
    quireCoord:function(n){
      this.addWindow();
      if(n == this.article[this.article.length-1].id){
        this.col_width = $(".item").width();//item
        let arr = [];
        let str = []
        $(".item").each(function(){
          arr.push(Math.ceil($(this).height()));
          str.push({left:'0',top:'0'})  //item
        });
        this.item_height = [].concat(arr);//item
        //this.item_coor = [].concat(str); //item
        for(let i = 0;i<this.col;i++){
          this.col_height[i] = 0; //
        }
        this.addCoord()
      }
    },
    addCoord:function(){
      for(let a = 0; a<this.article.length; a++){
        var min = Math.min.apply(Math,this.col_height); //
        var sub = this.col_height.indexOf(min)  //
        this.item_coor[a].left = this.col_width*sub  //itemleft
        this.item_coor[a].top = this.col_height[sub];  //itemtop
        this.col_height[sub] += this.item_height[a];   //  item
        this.col_height[sub] += this.item_btm; //  
        var max = Math.max.apply(Math,this.col_height); 
        this.max_height = max;
      }
    },
    //
    addWindow:function(){
      if(this.screenWidth < 750){
          this.col = 1;
      }else{
        this.col = 2;
      }
    }
  },
  beforeCreate(){
    //data
  },
  created() {
    //,,data
    for (let i = 0;i < this.article.length;i++){
     this.item_coor.push({left:'0',top:'0'}) 
    }
  },
  mounted() {
    //
    this.quireCoord(this.article[this.article.length-1].id);
    const that = this
  window.onresize = () => {
    return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
        this.quireCoord(this.article[this.article.length-1].id);
      })()
    }
  }
};
</script>

<style scoped>
  .swiper-video{
    padding: 30px 10px;
    background: #fafafa;
  }
  .sub-content{
    margin-top: 10px;
    padding: 0 10px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    background: #eee;
  }

  .sub-title{
    font-size: 1.2rem;
    line-height: 3;
    font-weight: bold;
    color: #000;
    letter-spacing: 1px;
  }
  .sub-text{
    width: 68%;
  }
  .person{
    width: 30%;
  }

  .a-content{
    margin-top: -.5rem;
    width: 100%;
    position: relative;
  }

  .item{
      z-index: 10;
      transition: 0.8s;
      width: 50%;
      position: absolute;
  }
  .item div{
    width: 100%;
    box-sizing: border-box;
    padding: .5rem ;
  }
 
  .item div a{
    display: inline-block;
    background: #fafafa;
    padding: .5rem;
    border-radius: 3px;
    position: relative;
    
  }
  .a-img{
    overflow: hidden;
  }
  .a-img img{
    display: block;
    transition: all .5s;
  }
  
  .a-title{
    font-size: 1rem;
    line-height: 2;
    color: #333;    
    font-weight: bold;
    letter-spacing: 1px;
    transition: all .3s;
  }
  .a-time{
    font-size: .8rem;
    line-height: 1.5;
    color: #f00;    
    padding: 0 1rem;
    letter-spacing: 1px;
  }
  .a-text{
    padding: 0 1rem;
    color: #666;
    line-height: 1.5;
    font-size: .8rem;
    letter-spacing: 1px;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    transition: all .3s;
  }
  .a-gauze{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-box-shadow: rgba(0,0,0,0.5) 0px .3rem .3rem;
    -moz-box-shadow: rgb(0,0,0,0.5) 0px .3rem .3rem;
    box-shadow: rgba(0,0,0,0.5) 0px .3rem .3rem;
    opacity: 0;
    transition: all .5s;
    z-index: -1;
  }
  .item div:hover a .a-gauze{
    opacity: 1;
  }
  .item div:hover a .a-title{
    color: #f00;
  }
  .item div:hover a .a-text{
    color: #000;
  }
  .item div:hover a .a-img img{
    transform: scale(1.1);
  } 
  @media (max-width: 1200px) {
    .person{
      display: none;
    }
    .sub-text{
      width: 100%;
    }
  }
  @media (max-width: 750px) {
  .sub-content{
    padding: 10px 0;
  }
  .swiper-video{
    padding: 10px 10px;
    background: #fafafa;
    animation: bounceInDown .5s;
  }
  .item{
    width: 100%;
  }
  }
</style>



